<script>
  import {
    Tile,
    OrderedList,
    UnorderedList,
    ListItem,
    Checkbox,
    Button,
    TileGroup,
    RadioTile,
    NumberInput,
    RadioButtonGroup,
    RadioButton,
    TextInput,
    TextArea,
    Link,
    Modal,
    Grid,
    Row,
    Column,
    ImageLoader,
    InlineLoading,
    InlineNotification,
  } from "carbon-components-svelte";

  import DocumentTasks20 from "carbon-icons-svelte/lib/DocumentTasks20";

  import ImageUploader from "./ImageUploader.svelte";
  var imageDone;

  var formData = {
    user: undefined,
    images: undefined,
    productType: undefined,
    livingType: undefined,
    floorHeight: 1,
    buriedPipe: undefined,
    uninstallOption: undefined,
    comment: " ",
    regDate: undefined,
  };

  var isValid = {
    user: true,
    images: true,
    productType: true,
    livingType: true,
    floorHeight: true,
    buriedPipe: true,
    uninstallOption: true,
  };

  var resultModalHeader = "";
  var resultModalOpen = false;
  var resultModalMessage = "";

  var privacyCheck = false;

  var exampleModalOpen = false;
  var exampleModalNum = 0;

  const getDate = () => {
    const now = new Date();
    let date = `${now.getFullYear()}년 ${
      now.getMonth() + 1
    }월 ${now.getDate()}일 ${now.getHours()}시 ${now.getMinutes()}분 ${now.getSeconds()}초`;
    console.log(date);
    return date;
  };

  var submitted = false;

  const handleSubmit = async () => {
    formData.regDate = await getDate();
    for (let key of Object.keys(isValid)) {
      isValid[key] = true;
    }
    // 개인 정보 동의
    if (!privacyCheck) {
      resultModalHeader = "앗!";
      resultModalMessage =
        "개인 정보 활용에 동의해주셔야 서비스를 이용하실 수 있어요.";
      resultModalOpen = true;

      return;
    }

    // 이메일 정규식
    var regExp =
      /^[0-9a-zA-Z]([-_.]?[0-9a-zA-Z])*@[0-9a-zA-Z]([-_.]?[0-9a-zA-Z])*.[a-zA-Z]{2,3}$/i;
    if (formData.user.match(regExp) == null) {
      resultModalHeader = "앗!";
      resultModalMessage = "이메일 형식을 확인해주세요.";
      resultModalOpen = true;
      isValid.user = false;
      return;
    }

    // 값이 없는 경우
    for (let key of Object.keys(formData)) {
      if (!formData[key]) isValid[key] = false;
    }
    console.log(isValid);

    // 이미지가 없는 경우
    if (!formData.images[0]) isValid.images = false;

    // 확인
    console.log(isValid);
    let readyToInsert = true;
    for (let key of Object.keys(isValid)) {
      if (!isValid[key]) readyToInsert = false;
    }

    if (!readyToInsert) {
      resultModalHeader = "앗!";
      resultModalMessage = "입력되지 않았거나, 잘못된 값이 있어요.";
      resultModalOpen = true;
      return;
    }

    //이미지가 업로드중인 경우
    if (!imageDone) {
      resultModalHeader = "앗!";
      resultModalMessage =
        "이미지를 업로드하고 있어요. 잠시 후 다시 시도해주세요.";
      resultModalOpen = true;
      isValid.images = false;
    } else {
      resultModalHeader = "접수";
      resultModalMessage = "작성하신 내용으로 접수할까요?";
      resultModalOpen = true;
    }
  };

  const insertToSheet = async () => {
    const url =
      "https://script.google.com/macros/s/AKfycbyAP_0zIC31QWibrnY8fDQtPo1J73x31iGnoGFBvfVl8LjqJdo/exec?";

    let query = Object.keys(formData)
      .map((k) => encodeURIComponent(k) + "=" + encodeURIComponent(formData[k]))
      .join("&");

    const options = {
      method: "GET",
      header: {
        Accept: "application/json",
        "Content-Type": "application/json",
        "Access-Control-Allow-Origin": "*",
      },
    };

    var result = await fetch(url + query, options);
    return result;
  };
</script>

<div>
  {#if !submitted}
    <div class="formItem" class:invalidData={!isValid.user}>
      <h5>이메일</h5>
      <TextInput
        bind:value={formData.user}
        labelText="연락받으실 이메일을 남겨주세요!"
        placeholder="yourname@example.com"
      />
    </div>
    <div class="formItem" class:invalidData={!isValid.images}>
      <h5>사진</h5>
      <Tile>
        견적을 위해 필요한 사진들을 첨부해주세요.<br />
        사진은 총 4장까지 첨부할 수 있습니다! (<span
          style="color:orange;font-weight: bold;">*</span
        >
        필수항목)
        <OrderedList style="padding-left:2em;margin-top:1em">
          <ListItem>
            실내기 사진 <span style="color:orange;font-weight: bold;">*</span>
          </ListItem>
          <ListItem>
            실외기 사진 <span style="color:orange;font-weight: bold;">*</span>
          </ListItem>

          <ListItem>배관 사진</ListItem>
          <ListItem>기타 도움이 되는 사진 (전경 등)</ListItem>
        </OrderedList>
        <div style="display:flex;justify-content: flex-end;">
          <div>
            <h6>사진 예시</h6>
            <p>
              <Link
                on:click={() => {
                  exampleModalOpen = true;
                  exampleModalNum = 0;
                }}>실내기</Link
              > , <Link
                on:click={() => {
                  exampleModalOpen = true;
                  exampleModalNum = 1;
                }}>실외기</Link
              > , <Link
                on:click={() => {
                  exampleModalOpen = true;
                  exampleModalNum = 2;
                }}>배관</Link
              >
            </p>
          </div>
        </div>
      </Tile>
      <Modal
        size="lg"
        passiveModal
        modalHeading="사진 예시"
        bind:open={exampleModalOpen}
      >
        {#if exampleModalNum == 0}
          <Grid style="width:100%">
            <Row>
              <Column>
                <span style="color:green;font-weight: bold;">Good</span>
                <ImageLoader
                  src="https://i.ibb.co/G3jLqpb/image-1.png"
                  alt=""
                  style="max-width:100%"
                />
              </Column>
            </Row>
            <Row>
              <Column>
                <span style="color:darkred;font-weight: bold;">Bad</span>
                <ImageLoader
                  src="https://i.ibb.co/y8wQTBm/image.jpg"
                  alt=""
                  style="max-width:100%"
                />
              </Column>
            </Row>
            <Row>
              <Column>
                <p>
                  실내기 설치 or 교체 예상 장소 사진 ( 해당 장소와 주변이 보이는
                  넓은 사진 )
                </p>
              </Column>
            </Row>
          </Grid>
        {:else if exampleModalNum == 1}
          <Grid style="width:100%">
            <Row>
              <Column>
                <span style="color:green;font-weight: bold;">Good</span>
                <ImageLoader
                  src="https://i.ibb.co/5hGpCLw/1.png"
                  alt=""
                  style="max-width:100%;width:auto;height:auto"
                />
              </Column>
              <Column>
                <span style="color:darkred;font-weight: bold;">Bad</span>
                <ImageLoader
                  src="
                https://i.ibb.co/WF2v64D/2.jpg"
                  alt=""
                  style="max-width:100%;width:auto;height:auto"
                />
              </Column>
            </Row>
            <Row>
              <Column>
                <p>
                  실외기와 배관이 설치 or 교체 예상되는 장소 사진<br />(ex. 야외
                  실외기 거치대 / 발코니(베란다) / 실내 실외기실)
                </p>
              </Column>
            </Row>
          </Grid>
        {:else if exampleModalNum == 2}
          <Grid style="width:100%">
            <Row>
              <Column>
                <span style="color:green;font-weight: bold;">Good</span>
                <ImageLoader
                  src="https://i.ibb.co/sPpC9qQ/image.png"
                  alt=""
                  style="max-width:100%;width:auto;height:auto"
                />
              </Column>
              <Column />
            </Row>
            <Row>
              <Column>
                <p>배관라인이 설치 or 교체 예상되는 부분의 사진</p>
              </Column>
            </Row>
          </Grid>
        {:else if exampleModalNum == 3}
          <Grid style="width:100%">
            <Row>
              <Column>
                <span style="color:green;font-weight: bold;">Good</span>
                <ImageLoader
                  src="https://i.ibb.co/sPpC9qQ/image.png"
                  alt=""
                  style="max-width:100%;width:auto;height:auto"
                />
              </Column>
              <Column />
            </Row>
            <Row>
              <Column>
                <p>배관라인이 설치 or 교체 예상되는 부분의 사진</p>
              </Column>
            </Row>
          </Grid>
        {/if}
      </Modal>
      <ImageUploader bind:images={formData.images} bind:done={imageDone} />
    </div>

    <!-- 상품 형태 -->
    <div class="formItem" class:invalidData={!isValid.productType}>
      <h5>에어컨 형태</h5>
      <RadioButtonGroup bind:selected={formData.productType}>
        <RadioButton labelText="벽걸이형" value="벽걸이" />
        <RadioButton labelText="스탠드형" value="스탠드" />
        <RadioButton labelText="2 in 1" value="2in1" />
      </RadioButtonGroup>
    </div>

    <!-- 주거 형태 -->
    <div class="formItem" class:invalidData={!isValid.livingType}>
      <h5>주거 형태</h5>
      <RadioButtonGroup
        bind:selected={formData.livingType}
        orientation="vertical"
      >
        <RadioButton labelText="원룸" value="원룸" />
        <RadioButton labelText="빌라" value="빌라" />
        <RadioButton labelText="오피스텔" value="오피스텔" />
        <RadioButton labelText="아파트" value="아파트" />
        <RadioButton labelText="기타" value="기타" />
      </RadioButtonGroup>
    </div>

    <!-- 층수 -->
    <div class="formItem" class:invalidData={!isValid.floorHeight}>
      <h5>층수</h5>
      <NumberInput
        bind:value={formData.floorHeight}
        min="0"
        step="1"
        label="몇 층에 거주하고 계신가요?"
      />
    </div>

    <!-- 매립 배관 여부 -->
    <div class="formItem" class:invalidData={!isValid.buriedPipe}>
      <h5>매립배관 여부</h5>
      <TileGroup bind:selected={formData.buriedPipe}>
        <RadioTile value="맞음">매립배관이에요.</RadioTile>
        <RadioTile value="아님">매립배관이 아니에요.</RadioTile>
        <RadioTile value="모름">모르겠어요.</RadioTile>
      </TileGroup>

      <div style="display:flex;justify-content: flex-end;">
        <Button kind="ghost" size="field" style="margin:0;padding:1em"
          >매립배관이란?</Button
        >
      </div>
    </div>

    <!-- 철거 후 조치 -->
    <div class="formItem" class:invalidData={!isValid.uninstallOption}>
      <h5>기존 제품 철거 후 처리 여부</h5>
      <TileGroup bind:selected={formData.uninstallOption}>
        <RadioTile value="보관">사용하던 제품은 보관할게요.</RadioTile>
        <RadioTile value="폐기">수거 및 폐기해주세요.</RadioTile>
        <RadioTile value="기존제품 없음">사용하던 제품이 없어요.</RadioTile>
      </TileGroup>
    </div>

    <!-- 기타 의견 -->
    <div class="formItem">
      <h5>기타 요청사항</h5>
      <TextArea
        bind:value={formData.comment}
        labelText="기타 요청사항, 문의, 추가 설명등을 자유롭게 남겨주세요!"
      />
    </div>
    <!-- 개인 정보 활용 동의 -->
    <div class="formItem">
      <h5>개인 정보 활용 동의</h5>
      <Tile>
        <div>
          (주)프로젝트일삼삼일은 해당 서비스를 운영하고 마케팅 정보에 활용함에
          있어 개인정보 보호를 매우 중요시하며, [개인정보 보호법] 등 개인정보와
          관련된 법령 상의 개인정보 보호 규정을 준수하고 있습니다.
        </div>
        <UnorderedList style="padding-left:2em;margin-top:1em;">
          <ListItem>수집 항목 : 이메일, 사진 및 기타 추가 수집 내용</ListItem>
          <ListItem
            >수집 및 이용 목적 : 서비스 제공, 마케팅 홍보에 활용</ListItem
          >
          <ListItem>보유 및 이용 기간 : 서비스 종료시까지</ListItem>
        </UnorderedList>
      </Tile>
      <Checkbox
        style="padding:1em"
        labelText="개인정보 활용에 동의합니다."
        bind:checked={privacyCheck}
      />
    </div>

    <div class="formItem" style="text-align: center;">
      <Button style="width:100%" icon={DocumentTasks20} on:click={handleSubmit}
        >견적 문의하기</Button
      >
    </div>

    <Modal
      size="xs"
      bind:open={resultModalOpen}
      modalHeading={resultModalHeader}
      primaryButtonText={resultModalHeader == "앗!" ? "확인" : "접수"}
      secondaryButtonText={resultModalHeader == "앗!" ? "" : "취소"}
      on:click:button--secondary={() => {
        resultModalOpen = false;
      }}
      on:click:button--primary={resultModalHeader == "앗!"
        ? () => {
            resultModalOpen = false;
          }
        : () => {
            submitted = true;
          }}
    >
      <p>{resultModalMessage}</p>
    </Modal>
  {:else}
    <div
      style="margin: 5em 5em; display:flex;justify-content: center; align-items: center;"
    >
      {#await insertToSheet()}
        <div style="display:flex;justify-content: center; align-items: center;">
          <InlineLoading />
        </div>
        <p style="text-align:center">소중한 의뢰를 등록하는 중...</p>
      {:then result}
        {#if result.ok}
          <InlineNotification
            lowContrast
            kind="success"
            title="성공"
            subtitle="접수를 완료했어요! 견적 결과는 이메일로 알려드릴게요."
          />
        {:else}
          <Grid>
            <Row>
              <Column>
                <InlineNotification
                  lowContrast
                  kind="error"
                  title="오류"
                  subtitle="죄송합니다, 서버와 연결 중 오류가 발생했어요. 아래의 링크를 통해 카톡으로도 접수하실 수 있어요."
                />
              </Column>
            </Row>
            <Row>
              <Column>
                <a href="http://project1331.oopy.io/install">
                  <img
                    src="./images/kakao.png"
                    alt=""
                    style="max-width:100%; margin-top:2em"
                  />
                </a>
              </Column>
            </Row>
          </Grid>
        {/if}
      {/await}
    </div>
  {/if}
</div>

<style>
  h5 {
    margin-top: 1em;
    margin-bottom: 1em;
  }
  .formItem {
    margin-bottom: 1em;
    padding: 1em;
  }
  :global(.bx--modal-content) {
    padding-left: 2% !important;
    padding-right: 2% !important;
  }
  .invalidData {
    background-color: #ffefef;
  }
</style>
