<script>
  import {
    FileUploaderDropContainer,
    FileUploaderButton,
    Row,
    Grid,
    Column,
    Tile,
    AspectRatio,
    ImageLoader,
    InlineLoading,
    Button,
    Modal,
    Tooltip,
  } from "carbon-components-svelte";

  import { imgbbUploader } from "./imgbbUploader";

  import "carbon-components-svelte/css/all.css";

  export let images = [];
  export let done = false;

  var status = [];
  var files;
  const resetFile = () => {
    uploader.value = "";
    images = [];
    status = [];
  };

  const handleUpload = async () => {
    files = uploader.files;
    done = false;
    if (files.length > 4) {
      modalOpen = true;
      files = undefined;
      uploader.value = "";
    } else {
      for (var i = 0; i < files.length; i++) {
        status[i] = "active";
      }
      for (var i = 0; i < files.length; i++) {
        let result = await imgbbUploader(files[i]);
        if (result.success == true) {
          status[i] = "finished";
          images[i] = result.data.display_url;
        } else {
          status[i] = "error";
        }
      }
      done = true;
    }
  };

  var uploader;

  var modalOpen = false;
</script>

<Modal
  bind:open={modalOpen}
  modalHeading="앗!"
  primaryButtonText="확인"
  on:click:button--primary={() => (modalOpen = false)}
>
  사진은 최대 4장까지 업로드하실 수 있어요.
</Modal>
<div style="margin-top:1em;margin-bottom:1em">
  {#if files}
    <div
      style="display:flex;justify-content: center;align-items: center; margin-bottom: 1.5em;"
    >
      {#each files as file, i}
        <div
          style="display:flex;flex-direction: column; margin-left:0.5em; margin-right:0.5em"
        >
          <div
            style="display:flex;justify-content: center;align-items: center;width:80px; height:80px; background-color:#f2f2f2;border-radius:5px;"
          >
            <ImageLoader
              src={URL.createObjectURL(file)}
              style="max-width:75px;max-height:75px;width:auto;height:auto"
            />
          </div>
          <div
            style="display:flex;justify-content: center;align-items: center;"
          >
            <div>
              <InlineLoading bind:status={status[i]} />
            </div>
          </div>
        </div>
      {/each}
    </div>
  {/if}
  <div style="display:flex; justify-content:flex-end; align-items:center">
    {#if files}
      <div>
        <Tooltip open="true" direction="left" style="margin-right:1em">
          <p>사진을 변경하시려면<br />버튼을 다시 눌러주세요!</p>
        </Tooltip>
      </div>
    {/if}
    <div>
      <FileUploaderButton
        bind:ref={uploader}
        labelText="사진 첨부하기"
        accept={[".jpg", ".jpeg", ".png", ".gif"]}
        on:change={handleUpload}
        multiple
      />
    </div>
  </div>
</div>

<style>
